import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { PayrollUtangJenisService } from 'src/app/sdk/payroll/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { Utang_jenis } from 'src/app/sdk/payroll/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private payrollUtangJenisService: PayrollUtangJenisService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }


    ngOnInit() {
        this.form();
    }

    komponen: FormGroupTypeSafe<Utang_jenis>;
    form() {
        this.komponen = this.fb.group<Utang_jenis>(
            {
                kode_utang_jenis: new FormControl('', [Validators.required]),
                nama_utang_jenis: new FormControl('', [Validators.required]),
                penamaan_slip_gaji: new FormControl('', [Validators.required]),
                ditampilkan_slip_gaji: new FormControl('ya', [Validators.required]),
                status_utang_jenis: new FormControl('ya', [Validators.required]),
                keterangan_utang_jenis: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                id_user: new FormControl('')
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.komponen.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.komponen.invalid) {
            console.log(this.komponen.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.payrollUtangJenisService.postPayrollUtangJenis(this.komponen.value).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/utang'])
    }


}
